import { Component, OnInit } from '@angular/core';
import { Serie } from 'src/app/services/interfaces/serie';
import { ActivatedRoute } from '@angular/router';
import { DataSerieService } from 'src/app/services/data-serie.service';
import { NavController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth.service';

@Component({
	selector: 'app-info-add-serie',
	templateUrl: './info-add-serie.page.html',
	styleUrls: [ './info-add-serie.page.scss' ]
})
export class InfoAddSeriePage implements OnInit {
	selectedSerie: Serie;

	constructor(
		private navCtrl: NavController,
		private serviceSerie: DataSerieService,
		private authService: AuthService,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit() {
		this.activatedRoute.paramMap.subscribe((paramMap) => {
			if (!paramMap.has('idSerie')) {
				this.navCtrl.navigateBack([ '/selectionner-serie' ]);
				return;
			}
			this.selectedSerie = { ...this.serviceSerie.getInAllSerie(paramMap.get('idSerie')) };
		});
		this.selectedSerie.note = 2;
		this.selectedSerie.vuEntierement = true;
	}
	goBack() {
		this.navCtrl.navigateBack([ '/selectionner-serie' ]);
	}

	isFinished(s: Serie) {
		return !(s.dateF === undefined);
	}

	Valider() {
		console.log(this.selectedSerie);
		this.serviceSerie.addInMySerie(this.selectedSerie, this.authService.userId);
		this.navCtrl.navigateBack([ '/medias/series' ]);
	}

	passerNote(note: number) {
		this.selectedSerie.note = note;
	}
}
